/* https://www.paulirish.com/2012/box-sizing-border-box-ftw/ */
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

/* . */
body {
  padding: 0;
  overflow-y: hidden;
}

html, body, #root {
  height: 100%;
  margin:0;
}

/* By TAG type */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: .5rem;
}

/******** General Purpose ********/
.hidden {
  display: none !important;
}

.site--small {
  font-size: 85%;
}

.site--narrow {
  line-height: 1.25; /* aot 1.75 */
}

/******** Site Theme ********/

#root {
  /* Controls the majority of lengths on the site. Capped at [iPod: 1024x768] via @media (see below) */
  font-size: 2.5vmin;
  overflow-y: scroll;
  background: /* linear-gradient(rgba(47, 23, 15, 0.65), rgba(47, 23, 15, 0.65)), */ url("./img/bg.jpg");
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}
@media(min-width: 768px) {
  #root {
    font-size: 19.2px;
  }
}

em {
  font-size: 112.5%;
}

p, .site--par {
  line-height: 1.75;
  margin-top: 1em;
}

.site--has-float:after {
  content: '';
  display: block;
  clear: both;
}

.site--text-primary {
  color: hsl(34, 74%, 67%);
}

/******** Tables ********/

.site--table {
  display: table;
  width: 100%;
  border-collapse: collapse;
  /* border-spacing: 2ex 0; Use padding instead to achieve styles that work for tables or normal divs (cfr .site--table.site--as-list)*/
}
.site--table.site--as-list {
  width: auto;
}
/**** with actual <table> ****/
.site--table > tbody > tr > td {
  display: table-cell;
  border-bottom: .5ex solid hsla(34, 74%, 17%, .3);
}
.site--table.site--last-right-aligned > tbody > tr > td:last-child {
  font-size: 112.5%;
  padding-left: 2ex;
  text-align: right;
}
/**** with generic tags (div..) ****/
.site--table > .site--trow {
  display: table-row;
}
.site--tcell, .site--table > .site--trow > .site--tcell {
  display: table-cell;
}
.site--table.site--last-right-aligned > .site--trow > .site--tcell:last-child {
  font-size: 112.5%;
  padding-left: 2ex;
  text-align: right;
}

/******** Lists ********/

.site--as-list {
  margin-top: 1em; /* cfr. ul:margin-top browser default */
}
.site--as-list > .site--as-list-item,
.site--table.site--as-list .site--tcell:first-child {
  padding-left: 2ex;
}
.site--as-list > .site--as-list-item:before,
.site--table.site--as-list .site--tcell:first-child::before {
  content: '\2022';
  padding-right: 1ex;
}

a, .site--internal-link {
  font-size: 112.5%;
  margin: -.5ex;
  padding: .5ex;
  cursor: pointer;
  color: hsl(214, 74%, 33%);
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}
a:hover, .site--internal-link:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(64,16,4,0.125)), to(rgba(64,16,4,0.25)));
  background-image: -webkit-linear-gradient(rgba(64,16,4,0.125), rgba(64,16,4,0.25));
  background-image: -o-linear-gradient(rgba(64,16,4,0.125), rgba(64,16,4,0.25));
  background-image: linear-gradient(rgba(64,16,4,0.125), rgba(64,16,4,0.25));
}

.site--offset > li {
  /* list-style: circle; */
  padding-left: 2ex;
}

/******** Buttons ********/

.site--button {
  padding: .75ex;
  text-align: center;
  vertical-align: middle;
  background: hsla(34, 74%, 67%,.75);
  cursor: pointer;
  border-radius: 1em;
}

/******** Misc. ********/

.site--ilist {
  display: inline-block;
}
.site--ilist:not(:last-child):after {
  content: ',\00A0';
}

/* Useful inside a text-align center / right block */
.site--wrap-left {
  display: inline-block;
  text-align: start;
}
.site--wrap-left.site--table {
  display: table;
}



