.app--main {
  height: 100%; /* for Sticky footer */
  /* Center with margin. `vmin` should work even in IE */
  width: 85vmin;
  min-width: 50vw;
  margin: 0 auto;
  padding: 20px;
  /* . */
  font-family: Lora,
    /* -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto, */
    "Helvetica Neue",
    Arial,
    sans-serif;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: left;
    /* background: radial-gradient(closest-side, rgba(73, 42, 29, 1), transparent); */
    background: rgba(73, 42, 29, 0.25);
}

.app--heading {
  padding-top: 4em;
  padding-bottom: 4em;
  padding-top: 8vmin;
  padding-bottom: 8vmin;
  line-height: 1; /* TODO */
  text-align: center;
  color: #FFF;
  text-transform: capitalize;
  text-shadow: 0 0 3ex #300;
}
.app--heading .app--heading-upper {
  margin-bottom: 1em;
  /* font-size: 2rem; */
  font-weight: 800;
}
.app--heading .app--heading-lower {
  font-size: 400%;
  font-size: 8vmin;
  font-weight: 100;
  line-height: 1;
}

/* for Sticky footer */
/* .Appcontainer {
