} */

.app--footer {
  /* margin-top: -83px; /* CALC: - .app--footer:height * */
  padding: 3ex;
  font-size: 80%;
  font-weight: 400;
  text-align: center;
  background-color: hsl(18, 43%, 20%);
  opacity: .85;
  color: #FFF;
}

.app--module {
  padding: 6vmin;
  background-color: rgba(255, 255, 255, 0.85);
}

